import { onMounted, ref, computed } from "vue";
import { getSelfPins, removePin } from "@/pages/background/pins";
import { sleep } from "@/pages/background/tool";
export default {
  __name: 'RemoveAllPins',
  setup(__props, {
    expose
  }) {
    expose();
    let pins = ref([]);
    let remove = ref(null);
    let loading = ref(true);

    // 
    let buttonLoading = computed(() => {
      return loading.value || !!remove.value;
    });
    // 
    let buttonText = computed(() => {
      if (remove.value) return `${remove.value.msg_Info.content.slice(0, 20)}...${pins.value.length}`;
      if (loading.value) return ``;
      if (!pins.value.length) return "";
      return `${pins.value.length}`;
    });
    const removeAllPins = async () => {
      if (confirm("")) {
        while (pins.value.length) {
          remove.value = pins.value.shift();
          await sleep(0.1);
          let {
            success,
            err_msg
          } = await removePin(remove);
          if (success) {
            await sleep(1);
          } else {
            pins.value.unshift(remove.value);
            alert(err_msg);
            break;
          }
        }
        remove.value = null;
      }
    };
    onMounted(async () => {
      loading.value = true;
      pins.value = await getSelfPins();
      loading.value = false;
    });
    const __returned__ = {
      get pins() {
        return pins;
      },
      set pins(v) {
        pins = v;
      },
      get remove() {
        return remove;
      },
      set remove(v) {
        remove = v;
      },
      get loading() {
        return loading;
      },
      set loading(v) {
        loading = v;
      },
      get buttonLoading() {
        return buttonLoading;
      },
      set buttonLoading(v) {
        buttonLoading = v;
      },
      get buttonText() {
        return buttonText;
      },
      set buttonText(v) {
        buttonText = v;
      },
      removeAllPins,
      onMounted,
      ref,
      computed,
      get getSelfPins() {
        return getSelfPins;
      },
      get removePin() {
        return removePin;
      },
      get sleep() {
        return sleep;
      }
    };
    Object.defineProperty(__returned__, '__isScriptSetup', {
      enumerable: false,
      value: true
    });
    return __returned__;
  }
};