import { ref, onMounted, computed } from 'vue';
import { initUrlInfo } from "@/utils";
import { getUserZanPins, cancelZanPin } from "@/pages/background/pins";
import { sleep } from "@/pages/background/tool";
export default {
  __name: 'CancelAllPinsZan',
  setup(__props, {
    expose
  }) {
    expose();
    let loading = ref(false);
    let remove = ref(null);
    let pins = ref([]);
    let count = ref(0);
    let removeCount = ref(0);

    // 
    let buttonLoading = computed(() => {
      return loading.value || !!remove.value;
    });
    // 
    let buttonText = computed(() => {
      if (remove.value) return `${remove.value.msg_Info.content.slice(0, 20)}...${removeCount.value}`;
      if (loading.value) return ``;
      if (!pins.value.length) return "";
      return `${count.value}`;
    });
    const cancelAllPins = () => {
      const cancel = async () => {
        if (!pins.value.length) return;
        while (pins.value.length) {
          remove.value = pins.value.shift();
          await sleep(0.1);
          let {
            success,
            err_msg
          } = await cancelZanPin(remove.value);
          if (success) {
            removeCount.value++;
            await sleep(1);
          } else {
            pins.value.unshift(remove.value);
            removeCount.value = 0;
            alert(err_msg);
            break;
          }
        }
        remove.value = null;
        // 
        if (pins.value.length) return;
        // 
        await load();
        cancel();
      };
      if (confirm("")) {
        cancel();
      }
    };
    const load = async () => {
      loading.value = true;
      let urlInfo = await initUrlInfo();
      let res = await getUserZanPins(urlInfo.info.userId);
      count.value = res.count;
      pins.value = res.pins;
      loading.value = false;
    };
    onMounted(async () => {
      await load();
    });
    const __returned__ = {
      get loading() {
        return loading;
      },
      set loading(v) {
        loading = v;
      },
      get remove() {
        return remove;
      },
      set remove(v) {
        remove = v;
      },
      get pins() {
        return pins;
      },
      set pins(v) {
        pins = v;
      },
      get count() {
        return count;
      },
      set count(v) {
        count = v;
      },
      get removeCount() {
        return removeCount;
      },
      set removeCount(v) {
        removeCount = v;
      },
      get buttonLoading() {
        return buttonLoading;
      },
      set buttonLoading(v) {
        buttonLoading = v;
      },
      get buttonText() {
        return buttonText;
      },
      set buttonText(v) {
        buttonText = v;
      },
      cancelAllPins,
      load,
      ref,
      onMounted,
      computed,
      get initUrlInfo() {
        return initUrlInfo;
      },
      get getUserZanPins() {
        return getUserZanPins;
      },
      get cancelZanPin() {
        return cancelZanPin;
      },
      get sleep() {
        return sleep;
      }
    };
    Object.defineProperty(__returned__, '__isScriptSetup', {
      enumerable: false,
      value: true
    });
    return __returned__;
  }
};